<template>
  <div class="login">
    <div class="center">
      <div class="logo">
        <img src="../../../assets/login-logo.png" alt="" />
      </div>

      <div>
        <van-field v-model="username" type="text" placeholder="请输入手机号" />
        <van-field
          v-model="password"
          type="password"
          placeholder="请输入密码"
        />

        <van-button class="btn" type="info" block @click="submit"
          >登录</van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import { login as loginApi } from '@/api/index';
import { checkMobile, checkPassword } from '@/utils/index';



export default {
  components: {},
  data() {
    return {
      username: '',
      password: '',
      sms: '',

      ispd: true
    };
  },
  methods: {
    gotoRest() {

    },
    submit() {
      const check1 = checkMobile(this.username);
      if (check1.code) {
        Toast(check1.msg);
        return;
      }

      const check2 = checkPassword(this.password);
      if (check2.code) {
        Toast(check2.msg);
        return;
      }

      loginApi({ username: this.username, password: this.password }).then(res => {
        if (res) {
          if (res.data.code === 0) {
            localStorage.setItem('isLogin', true);
            localStorage.setItem('userInfo', JSON.stringify(res.data.data));
            this.$store.commit('setUserInfo', res.data.data);
            this.$router.push({
              path: '/'
            });
          } else {
            this.$Toast(res.data.msg)
          }
        } else {
          this.$Toast('登录失败')
        }
      });
    }
  }
};
</script>

